
.synthesise{
  padding:  20px 0px;
  background-color: #fbfbfb;
  overflow: hidden;
}
.synthesise ul{
width: 100%;
}
.synthesise .grid{

float: left;
padding: 5px;
/* margin-left: 3%; */
}
.synthesise ul li:first-child{
  margin-left: 0;
}
.synthesise .grid a{
float: left;
  display: inline-block;
font-size: 12px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 33.33333%;
    padding-left: 4%;
    padding-right: 4%;
    margin-bottom: 20px;
    overflow: hidden;
}
.synthesise .grid .img {
  height: 120px;

  overflow: hidden;
  border: 5px solid #fff;
  -webkit-box-shadow: 0 0 3px #a6a5a5;
          box-shadow: 0 0 3px #a6a5a5;
  border-radius: 3px;
}
.synthesise .grid .img img{
  width: 100%;
}
.synthesise .grid a div{
}
.synthesise .grid .name{
 margin-top: 8px;
 overflow: hidden;
  text-overflow: ellipsis;
   white-space: nowrap;
  color: #000;
}
.synthesise .grid .cove{
  margin-top: 10px;
  font-size: 11px;
}



