
@import './iconfont.css';
.active{
  opacity: 1 !important;
}
.header_red{
}
.header_headers{
    overflow:hidden;
    background-color:#fb5b81;
    height:2.7rem;
    -webkit-box-shadow:0 0 3px #bc6b7f;
            box-shadow:0 0 3px #bc6b7f;
    width:100%;
}
.header_headers span{
    width:30%;
}
.more_icon{
    float:left;
}
.more_icon img{
    height:1.5rem;

    margin-left:0.5rem;
    margin-top:0.6rem;
}
.header_headers ul{
    float:left;
    width:50%;
    margin-left:0;
    padding:0;
    text-align:center;
}
.header_headers ul li i{
    font-size:1.5rem;
    opacity: 0.5;
    color:#fff;
}
.header_headers ul li {
    float:left;
        padding-top:0.6rem;
    text-align:center;
    width:33.333%;
}
.header_headers ul .router-link-active li i{
       opacity:1;
}



