
.vogue{

width: 100%;
overflow: hidden;
background-color: #f2f2f2;
}
.vogue  ul{
padding: 10px;
width: 100%;
display: inline-block;
}
.vogue  ul li{
  /* padding: 10px; */
  height: 100px;
  margin: 10px 0;
 margin-right: 10px;
  padding-bottom: 10px;
  border-bottom:1px solid #d2d2d2;
  overflow: hidden;
}
.vogue .vogue-img{
  width: 100px;
  padding-right: 15px;
  float:left;
}
.vogue .vogue-img img{
  width: 100%;
}
.vogue .vogue-text{
  float: left;

  font-size: 13px;
}
.vogue .vogue-text div{
  margin-top: 10px;
  color: #717171;
}
.vogue .vogue-text div:first-child{
  font-weight: bold;
  color: #000;
}



