
.heat{
  background:#f2f2f2;
  width:100%;

  overflow: hidden;
}
.heat-content{
padding: 3px;
}
.heat-img{
  height: 145px;
    padding-left:3%;
  padding-right: 3%;
  min-height: 87px;
  overflow: hidden;
}
.heat ul{
  padding: 6px 0;
  max-width: 660px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin:0 auto;
  width: 100%;
  overflow:hidden;
}
.heat ul li {

  width:33.33%;
  margin-bottom: 10px;
  float:left;
  overflow:hidden;
  /* padding:3px; */
}
.heat ul li img{
  width:100%;
  min-height: 150px;
}
.heat ul li .text{
  font-size:14px;
  padding:8px 0 4px 4px;
   white-space: nowrap;
  /*font-size: 0;*/
  overflow: hidden;
  text-overflow: ellipsis;
}
.heat ul li .scoring{
  font-size:12px;
  color:#6a6a6a;
  padding-left: 4px;
}



