
.top{
  background-color: #f2f2f2;
  padding-bottom: 10px;
}
.top ul li .top_id{
  height: 26px;
  color:#393939;
  padding-left: 6px;
  line-height: 26px;
  font-size: 13px;
}
.top ul li{
  overflow: hidden
}
.top ul li .top-content{
display: block;
padding: 6px;
padding: 6px  6px 0 6px;
height: 100px;
overflow: hidden;
/* padding: 0 6px; */
background-color: #fff;
width: 100%;
}
.top-content-img{
  float: left;
  width: 80px;
    margin-right: 15px;
  /* margin: 15px 1.12rem 0px; */
}
.top-content-img img{
  width: 100%;
  padding-bottom: 6px;
}
.top-content-test {
}
.top-content-test div:first-child{
  margin-top: 10px;
  font-weight: bold;
  font-size: 13px;
}
.top-content-test div{
  margin-top: 14px;
  font-size: 10px;
}
.top-content-img{
  padding-bottom: 6px;
  overflow: hidden;
  height: 88px;
overflow: hidden;
}
.top-content-test .average{
  color: #fa5b81;
}



